import React, {useState} from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
  timeline: {
    zIndex:-1,
  },
  paper: {
    padding: '6px 16px',
    color: theme.palette.primary.contrastText,
    backgroundColor: theme.palette.primary.main,
    transition: '0.5s, transform 2s',
  },
  neonBox:{
    color: theme.palette.primary.contrastText,
    boxShadow: "0 0 7px #aaa, 0 0 10px #bbb, 0 0 15px #ccc,"
              + "0 0 42px #78B2BA, 0 0 52px #78B2BA, 0 0 32px #78B2BA,"
              + "0 0 22px #37666C, 0 0 32px #37666C",
    padding: '6px 16px',
    backgroundColor: theme.palette.primary.main,
    transition: '0.5s, transform 2s',
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

function Item(props){
  const theme = useTheme();
  const classes = useStyles(theme);

  const [hover, setHover] = useState(false);

  function onEnter(){
    setHover(true);
    props.setSelectedSkills(props.el.skills);
  }

  function onLeave(){
    setHover(false);
    props.setSelectedSkills([]);
  }

  return (
    <TimelineItem>
      <TimelineOppositeContent>
        <Typography variant="body2" color="secondary">
          {props.el.start} - {props.el.end}
        </Typography>
      </TimelineOppositeContent>
    <TimelineSeparator>
      <TimelineDot>
        <props.el.icon/>
      </TimelineDot>
      {
        props.last ? null : <TimelineConnector />

      }
    </TimelineSeparator>
    <TimelineContent
        onMouseEnter={onEnter}
        onMouseLeave={onLeave}
    >
      <Paper elevation={3} className={[classes.paper, hover ? classes.neonBox : null]}>
        <Typography variant="h6" component="h1">
          {props.el.title}
        </Typography>
        <Typography>{props.el.text}</Typography>
      </Paper>
    </TimelineContent>
  </TimelineItem>
);



}

export default function CustomizedTimeline(props) {
    const theme = useTheme();
    const classes = useStyles(theme);

    
  return (
    <Timeline className={classes.timeline} align="alternate">
      {
        props.elements.map((el, idx) => { 
          return(<Item el={el} last={idx+1===props.elements.length} setSelectedSkills={props.setSelectedSkills} />)
        })
      }
    </Timeline>
  );
}